import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'


// import Lightbox from 'react-images'
import Gallery from "../components/Gallery"

import thumb01 from '../assets/images/thumbs_ML/tazab.jpg'
import thumb02 from '../assets/images/thumbs_ML/taza 2.jpg'
import thumb03 from '../assets/images/thumbs_ML/taza 3.jpg'
import thumb04 from '../assets/images/thumbs_ML/taza 4.jpg'

import full01 from '../assets/images/fulls/taza.jpg'
import full02 from '../assets/images/fulls/taza 2.jpg'
import full03 from '../assets/images/fulls/taza 3.jpg'
import full04 from '../assets/images/fulls/taza 4.jpg'

import qr from '../assets/images/QR_ML.jpg'

const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, caption: 'Photo 1', description: 'Set De Jardín De Infantes Personalizado En Quilmes Cole Kit'},
    { id: '2', src: full02, thumbnail: thumb02, caption: 'Photo 2', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '3', src: full03, thumbnail: thumb03, caption: 'Photo 3', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '4', src: full04, thumbnail: thumb04, caption: 'Photo 4', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'}
];

class HomeIndex extends React.Component {

    constructor() {
        super();
        this.handleSubmit = this.handleSubmit.bind(this);
      }

    handleSubmit(event) {
        event.preventDefault();
        const data = new FormData(event.target);
        
        fetch('https://formspree.io/dany2k6@gmail.com', {
          method: 'POST',
          body: data,
        });
      }


    render() {
        //const siteTitle = this.props.data.site.siteMetadata.title
        //const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                        <title>Roxieta - Contacto</title>
                        <meta name="description" />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header>
                            <h2>Contacto</h2>
                        </header>
                        <p>Dejanos tu consulta y te contestaremos lo más pronto posible. Gracias!</p>
                         

                           <form
                              action="https://formspree.io/dany2k11@gmail.com"
                              method="POST"
                              className="row"
                            >
                              <div className="col-lg-8" style={{ margin: '0 auto' }}>
                                <div className="row">
                                  <div className="form-group  col-md-6">
                                    <label htmlFor="_replyto">Email: </label>
                                    <input
                                      type="email"
                                      name="_replyto"
                                      className="form-control"
                                      required
                                    />
                                  </div><br /> 
                                  <div className="form-group col-md-6">
                                    <label htmlFor="name">Nombre: </label>
                                    <input name="name" className="form-control" required />
                                  </div><br /> 
                                </div>
                                <div className="form-group">
                                  <label htmlFor="message">Mensaje: </label>
                                  <textarea
                                    className="form-control"
                                    name="message"
                                    rows="10"
                                    required
                                  />
                                </div><br /> 
                                <div className="text-right">
                                  <input
                                    type="submit"
                                    value="Enviar Mensaje"
                                    className="btn btn-primary btn-submit"
                                  />
                                </div>
                              </div>
                            </form>


                          
                        <Link to="/">Volver</Link>
                        <br /><br />  

                    </section>

                 

                        
                    

                </div>

            </div>
        )
    }
}

export default HomeIndex


